.container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: transparent;
    display: flex;
    justify-content: center;
    z-index: 99;
    overflow-y: scroll;
    top: 0;
}

.filter {
    position: fixed;
    background: rgba(0, 0, 0, 0.8);
    width: 100vw;
    height: 100vh;

    padding: 40vw;
    top: -20vw;
    left: -20vw;
    /* background-color: orange; */
}

/* .filterComp {
    width: 130%;
    height: 130%;
    background-color: black;
    opacity: 0.7;
} */

.hoverWrap {
    width: 100vw;
    height: fit-content;
    display: flex;
    justify-content: center;
    /* background: rgba(0, 0, 0, 0.7); */
    z-index: 40;
}

.bgClick {
    width: 100vw;
    height: 100vh;
    position: fixed;
}

.hoverpage {
    width: 810px;
    height: fit-content;
    min-height: 500px;
    background-color: rgb(26, 26, 26);
    z-index: 60;
    margin-top: 80px;
    margin-bottom: 200px;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    overflow: hidden;

    box-shadow: 0px 5px 30px black;
}

.infoWrap {
    width: 100%;
    height: 100%;
}

.videoPlayer {
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 0px 0px 20px black;
    background-size: cover;
    background-color: black;
}

.videoPlayer img {
    /* width: auto; */
    height: 120%;

    pointer-events: none;
}

.infoBox {
    width: calc(100% - 4vw);
    height: fit-content;
    /* margin: 1.190vw; */
    padding: 2vw;
    /* padding: 2.3rem; */
    display: flex;
    flex-flow: row nowrap;
    margin-bottom: 10rem;
}

.coverImgWrap {
    width: 200px;
    /* width: 11.905vw; */
    height: 300px;
    /* height: 17.857vw; */
    overflow: hidden;
    border-radius: 5px;
    /* box-shadow: 5px 7px 10px black; */
    box-shadow: 0px 7px 10px black;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
}

.coverImgWrap img {
    width: auto;
    height: 100%;

    pointer-events: none;
}

.info {
    width: calc(100% - 200px - 1.5vw);
    margin-left: 1.5vw;
}

.buttons_title, .title, .summary, .genres {
    margin-bottom: 0.714rem;
    color: white;
}

.title {
    font-size: 1.5rem;
    font-family: "GMarket Bold";

}

.summary {
    font-size: 1rem;
    word-break: keep-all;
    /* font-family: "GMarket Light"; */
    line-height: 1.5rem;
}

.buttons_title {
    cursor: default;
}

.buttons_title {
    font-size: 1.5rem;
    font-family: "GMarket Bold";
}

.titleWrapperTwo {
    margin-top: 2rem;
    width: 100%;
}

/* .title {
    font-family: "SF Pro";
    font-weight: bold;
} */

.genreList {
    font-family: "GMarket Bold";
    color: grey;
    width: 3.5rem;
}

.genreBox {
    display: flex;
    flex-flow: row wrap;
}

.genresWrapper {
    display: flex;
    flex-flow: row nowrap;
    margin-top: 1.4rem;
    width: 100%;
}

.genres {
    margin-right: 0.8rem;
    color: grey;
    font-style: italic;
}

.buttons {
    width: 90%;
    height: 80%;
    /* margin: 10% 5%; */
    display: flex;
    flex-flow: row-reverse nowrap;
    justify-content: space-between;
    align-items: flex-end;
    /* align-items: baseline; */
    z-index: 30;
}

.xbutton {
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
}

.buttonsAlignment {
    display: flex;
    flex-flow: column nowrap;
}

.playAndRuntime {
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
}

.runtime {
    z-index: 40;
    color: white;
    font-size: 1rem;
    margin-left: 1rem;

    cursor: default;
}

.minutes {
    font-size: 0.85rem;
    margin-left: 3px;

    cursor: default;
}

.runtime::selection, .minutes::selection, .title::selection, .buttons_title::selection, .coverImgWrap img::selection {
    background: transparent;
}

@media (max-width: 900px) {
    .hoverpage {
        width: 90vw;
    }

    .videoPlayer {
        height: calc(80vw / 16 * 9);
    }

    .coverImgWrap {
        width: 22.222vw;
        height: 33.333vw;
    }

    .info {
        width: calc(100% - 22.222vw - 1.5vw);
        margin-left: 1.5vw;
    }

    .summary {
        word-break: break-all;
    }
}

@media (max-width: 600px) {
    .hoverpage {
        margin-top: 5.71428571rem;
    }
}

@media (max-width: 414px) {
    .hoverpage {
        width: 100vw;
        min-height: 100vh;
        max-height: fit-content;
        margin: 0;
    }

    .videoPlayer {
        height: calc(90vw / 16 * 9);
        position: fixed;
    }

    .infoBox {
        margin-top: calc(95vw / 16 * 9);
    }

    .info {
        width: calc(100% - 22.222vw - 20px);
        margin-left: 15px;
        margin-right: 10px;
    }

    .coverImgWrap {
        width: 22.222vw;
        height: 33.333vw;
        margin-left: 5px;
    }

    .buttons_title {
        font-size: 20px;
    }

    .summary {
        /* font-size: 1.2rem; */
        font-size: 14px;
        line-height: 20px;
    }

    .title {
        /* font-size: 1.6rem; */
        font-size: 17px;
    }
}